<template>
  <div>
    <div class="container">
      <h1>SubComponentes</h1>
    </div>
    <sub-component-table
        :data="subComponentListState"
        :dataComponentAirplane="componentAirplaneListState"
        @DeleteSubComponent="DeleteSubComponent"
        @addSubComponent="addSubComponent"
        @updateSubComponent="updateSubComponent"

    />
  </div>
</template>

<script>

import SubComponentTable from "@/components/inventaryManagement/subComponent/SubComponentTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "SubComponent",
  components: {
    SubComponentTable
  },
  data () {
    return {
      jsonDataUpdate: {
        componentId: 13,
        componentQuantity: 0,
        createdAt: '',
        createdBy: '',
        cycle_ext: 0,
        cycle_prox_insp: 0,
        cycle_ult_insp: 0,
        description: '',
        hor_ext: 0,
        hor_prox_insp: 0,
        hor_ult_insp: 0,
        id: 0,
        location: 0,
        makerID: 0,
        partNumber: '',
        prox_insp_date: '',
        status: '',
        timesTamp: '',
        totalCycles: 0,
        totalHours: 0,
        ult_insp_date: '',
        updatedAt: '',
        updatedBy: ''
      }
    }
  },
  created() {
    this.getSubComponentState()
    this.getComponentAirplaneState()
  },
  computed: {
    ...mapGetters(['subComponentListState', 'componentAirplaneListState'])
  },
  methods: {
    ...mapActions(['getSubComponentState', 'getComponentAirplaneState', 'setSubComponentnew', 'apiUpdateSubComponent', 'apiDeleteSubComponent']),

    addSubComponent( item) {
      this.setSubComponentnew(item)
    },
    updateSubComponent( item) {
      this.jsonDataUpdate.partNumber = item.partNumber;
      this.jsonDataUpdate.description = item.description;
      this.jsonDataUpdate.status = item.status;
      this.jsonDataUpdate.hor_ext = item.hor_ext;
      this.jsonDataUpdate.id= item.id;
      this.jsonDataUpdate.componentId = item.componentId;
      this.apiUpdateSubComponent(this.jsonDataUpdate)
    },
    DeleteSubComponent(id) {
      this.apiDeleteSubComponent(id)
    }
  }
}
</script>

