<template>
  <v-container>
    <v-data-table
        :headers="headers"
        :items="data"
        :search="search"
        sort-by="Tipo"
        class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
            flat
        >
          <v-col
              cols="8"
              sm="4"
              md="2"
          >
            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
            ></v-text-field>
          </v-col>

          <v-spacer></v-spacer>
          <v-dialog
              v-model="dialog"
              max-width="900px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
              >
                Añadir
              </v-btn>
            </template>
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
              <form @submit.prevent="submit">
                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>

                      <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="partNumber"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.partNumber"
                                :error-messages="errors"
                                label="Numero de partes"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="description"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.description"
                                :error-messages="errors"
                                label="Descripción"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="status"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.status"
                                :error-messages="errors"
                                label="Estado"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="hor_ext"
                              rules="required|max:10"
                          >
                            <v-text-field
                                v-model="editedItem.hor_ext"
                                :error-messages="errors"
                                label="Horas extras"
                                required
                            ></v-text-field>
                          </validation-provider>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                        >
                          <validation-provider
                              v-slot="{ errors }"
                              name="selectComponentAirplane"
                              rules="required"
                          >
                            <v-select
                                v-model="selectComponentAirplane"
                                :items="dataComponentAirplane"
                                :error-messages="errors"
                                label="Selecionar componente"
                                item-text="description"
                                item-value="id"
                                data-vv-name="selectComponentAirplane"
                                bottom
                                autocomplete
                                required
                            ></v-select>
                          </validation-provider>
                        </v-col>
                      </v-row>


                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn
                        class="mr-4"
                        type="submit"
                        color="#002F6C"
                        style="color: #E5F0F4;"
                        :disabled="invalid"
                    >
                      Guardar
                    </v-btn>
                  </v-card-actions>
                  <br/>

                </v-card>
              </form>
            </validation-observer>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5">¿Está seguro que desea eliminar?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
            small
            @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-container>

</template>

<script>
import { required,  max,  } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'
setInteractionMode('eager')

extend('required', {
  ...required,
  message: ' Campo no puede ir vacío',
})

extend('max', {
  ...max,
  message: ' No puede ser mayor que {length} caracteres',
})



export default {
  name:'TableSubComponent',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  props: ['data','dataComponentAirplane'],
  data: () => ({
    search: '',
    checkbox: null,
    dialog: false,
    dialogDelete: false,
    updateSubComponent: false,
    select: null,
    selectComponentAirplane: null,
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: 'id',
      },
      { text: 'Descripción', value: 'description' },
      { text: 'Estado', value: 'status' },
      { text: 'Número de partes', value: 'partNumber' },
      { text: 'Componentes', value: 'hor_ult_insp' },
      { text: 'Horas extras', value: 'hor_ext' },
      { text: 'Actions', value: 'actions', sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      componentId: 0,
      componentQuantity: 0,
      createdAt: '',
      createdBy: '',
      cycle_ext: 0,
      cycle_prox_insp: 0,
      cycle_ult_insp: 0,
      description: '',
      hor_ext: 0,
      hor_prox_insp: 0,
      hor_ult_insp: 0,
      id: 0,
      location: 0,
      makerID: 0,
      partNumber: '',
      prox_insp_date: '',
      status: '',
      timesTamp: '',
      totalCycles: 0,
      totalHours: 0,
      ult_insp_date: '',
      updatedAt: '',
      updatedBy: ''
    },
    defaultItem: {
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo SubComponent' : 'Editar SubComponent'
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  methods: {
    submit () {
      if (this.$refs.observer.validate()) {
        this.editedItem.componentId = this.selectComponentAirplane
        this.save()
      } else {
        console.log('completar los campos')
      }

    },

    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.selectComponentAirplane = this.editedItem.componentId
      this.updateSubComponent = true;
      this.dialog = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    deleteItemConfirm () {
      this.$emit('DeleteSubComponent', this.editedItem.id)
      this.closeDelete()
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      if(this.updateSubComponent) {
        this.$emit('updateSubComponent', this.editedItem)
      } else {
        this.$emit('addSubComponent', this.editedItem)
      }

      this.updateSubComponent = false
      this.close()
    },
  },
}
</script>
<style scoped>

</style>